@import url(https://fonts.googleapis.com/css?family=Comfortaa&display=swap);
@import url(https://fonts.googleapis.com/css?family=Comfortaa&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*Overall app styling*/
.App {
  margin: 0;
  background-color:white;
  height: 100%;
}
.App-header {
  color: white;
  text-align: center;
  background-color: #343a40;
}

/*Nav-bar styling(on every component)*/
.nav-img {
  width: 85px;
  border-radius: 20px;
  margin-top: 10px;
  margin-left: 11px;
  margin-bottom: 20px;
}
.icons {
  margin-left: 500px;
}

/*Home page styling*/
.welcome-text {

  text-align: center;
  margin-top: 50px;
  margin-bottom: 10px;
  font-family: 'Comfortaa', cursive;
}
.instruct-text {
  color: black;
  text-align: center;
  margin-top: 50px;
  font-family: 'Comfortaa', cursive;
}

/*Drop-down on homepage styling*/
.drop-background {
  height: 1900px;
}
.dropdown-button {
  margin-left:45%;
  position: fixed;
}



/*Search -bar styling*/
.container {
  margin: 36px auto;
  width: 80%;
  max-width: 800px;
  text-align: center;
}
.search-label {
  position: relative;
  width: 50%;
}
.container input {
  width: 100%;
  padding: 16px;
  font-size: 20px;
  font-style: italic;
  color: #444;
  box-sizing: border-box;
  outline: none;
}
.search-icon {
  position: absolute;
  top: 26px;
  font-size: 34px;
  color: #555;
  right: 18px;
}

/*Search results styling*/
.results-container {
  display: flex;
  flex-flow: wrap;
}
.result-item {
  display: block;
  position: relative;
  padding: 4px;
  border: 1px solid #898989;
  margin: 26px;
  text-align: center;
  min-width: 210px;
  box-shadow: 2px 2px 2px #898989;
}
.image-username {
  color: white;
  font-size: 18px;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 5px 10px;
 
}
.image {
  height: 95%;
  width: 200px;
}

/* loader */
.search-loading {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
}

/* show and hide */
.show {
  display: inline-block;
}
.hide {
  display: none;
}

/* Page Navigation stlying */
.nav-link-container {
  margin: 20px;
  display: flex;
  justify-content: flex-end;
}
.nav-link {
  color:white;
  background-color:  #343a40;
  text-decoration: none;
  border: 1px solid grey;
  padding: 10px 20px;
  margin-right: 31%;
}
.page-nav {
  margin-left: 320px;
}
.greyed-out {
  background: #f1f1f1;
  opacity: 0.5;
}
.btn {
  margin: 18px 5px 5px 5px;
}

/* favorite section styling */
.row-container {
  text-align: center;
}
.favourite-image {
  height: 40px;
  width: 40px;
}
.favorite {
  text-align: center;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  border-radius: 5px;
  height: 250px;
  width: 55%;
  overflow: hidden;
  /*border: 3px solid rgb(168, 168, 168);*/
  overflow-y: scroll;
}
.favorite-username {
  overflow-x: hidden;
  text-decoration: none;
  color: #343a40;
}

.play-icon {
  margin-left: 20px;
  margin-right: 20px;
}
.play-icon:hover {
  color: slateblue;
}


